import {Injectable} from '@angular/core';
import {HttpClient} from "@angular/common/http";
import {SingleTon} from "./SingleTon";
import {Observable} from "rxjs";
import {Result, Vacation, VoidResult} from "./models";

@Injectable({
  providedIn: 'root'
})
export class GenericService<M> {

  constructor(private http: HttpClient, private singleTon: SingleTon) {
  }

  getAll(): Observable<Result<M>> {
    return this.http.get<Result<M>>(`${this.singleTon.baseUrl}/vacation/getAll`);
  }

  getById(id): Observable<Result<M>> {
    return this.http.get<Result<M>>(`${this.singleTon.baseUrl}/vacation/getAll?id=${id}`);
  }

  delete(id): Observable<VoidResult> {
    return this.http.delete<VoidResult>(`${this.singleTon.baseUrl}/vacation/delete?id=${id}`);
  }

  save(model: M): Observable<VoidResult> {
    return this.http.post<VoidResult>
    (`${this.singleTon.baseUrl}/vacation/save`, model);
  }

  saveAndSendToWorkflow(model: M): Observable<VoidResult> {
    return this.http.post<VoidResult>
    (`${this.singleTon.baseUrl}/vacation/SaveAndSendToWorkflow`, model);
  }

}



@Injectable({
  providedIn: 'root'
})
export class VacationService extends  GenericService<Vacation>{

}
