export class VoidResult {
  Message;
  Type: ResultType;
}

export class Result<T> extends VoidResult {
  Grid: T[];
  SingleRecord: T;
}

export enum ResultType {
  Success,
  Fail
}


export class Vacation implements IModel {
  Id;
  Title;
  Type: VacationType;
}

export interface IModel {
  Id;
}


export enum VacationType {
  استحقاقی, استعلاجی, اشعه
}
